/* 갤러리 이미지 컨테이너 */
.gallery {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 썸네일 목록을 가로로 배치 */
.thumbnailContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3개씩 한 줄에 표시 */
  gap: 10px;
  width: 100%;
  margin-top: 20px;
}

.thumbnailItem {
  cursor: pointer;
  border-radius: 8px;
  overflow: hidden;
}

.thumbnailImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.2s ease;
}

.thumbnailItem:hover .thumbnailImage {
  transform: scale(1.1);
}

/* 모달 스타일 */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7); /* 배경을 반투명하게 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  pointer-events: none; /* 배경 클릭 비활성화 */
}

/* 모달 콘텐츠 영역 */
.modalContent {
  position: relative;
  width: 100%; /* 화면 가득 차게 */
  height: 100%; /* 화면 가득 차게 */
  background: transparent; /* 흰 배경을 없애고 투명 처리 */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  pointer-events: auto; /* 모달 내부 클릭 가능 */
}

/* 이미지 영역 */
.imageWrapper {
  display: flex;
  justify-content: center;
  position: relative;
  padding: 5vh 0; /* 위아래 여백을 일정하게 */
}

/* 메인 이미지 */
.mainImage {
  width: 100%;
  max-width: 100%;
  height: auto;
  max-height: 90vh; /* 화면 높이의 90%로 제한 */
  object-fit: cover; /* 비율을 유지하면서 화면에 맞게 조정 */
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

/* 네비게이션 버튼 */
.navButtonLeft,
.navButtonRight {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  border: none;
  color: white;
  font-size: 1.5rem; /* 버튼 크기 조정 */
  cursor: pointer;
  padding: 0;
  width: 40px; /* 모바일 화면에 적당한 크기로 설정 */
  height: 40px; /* 모바일 화면에 적당한 크기로 설정 */
  border-radius: 50%; /* 동그라미 모양 */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
}

/* 좌측 네비게이션 버튼 */
.navButtonLeft {
  left: 10px;
}

/* 우측 네비게이션 버튼 */
.navButtonRight {
  right: 10px;
}

.navButtonLeft:hover,
.navButtonRight:hover {
  background: rgba(0, 0, 0, 0.7); /* 호버 시 배경색 변경 */
}

/* 닫기 버튼 (X) */
.closeButton {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(0, 0, 0, 0.5); /* 불투명 배경 */
  border: none;
  font-size: 1.5rem; /* 버튼 크기 조정 */
  color: white;
  cursor: pointer;
  z-index: 1001;
  width: 40px; /* 모바일 화면에 적당한 크기로 설정 */
  height: 40px; /* 모바일 화면에 적당한 크기로 설정 */
  border-radius: 50%; /* 동그라미 모양 */
  display: flex;
  align-items: center;
  justify-content: center;
}

.closeButton:hover {
  background: rgba(0, 0, 0, 0.7); /* 호버 시 배경색 변경 */
}

/* 이미지 인덱스 (m/n 형식) */
.imageInfo {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 1rem;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5px 10px;
  border-radius: 8px;
  z-index: 1001;
}
